// proxyuni
const uniAsync = new Proxy({}, {
	get(target, name) {
		return (obj) => new Promise((resolve, reject) => {
			uni[name]({
				...obj,
				success: ret => {
					resolve(ret)
				},
				fail: err => {
					reject(err)
				}
			})
		})
	}
})

export default uniAsync
