/**
 * 
 */
class MessageSubscriber {
  constructor() {}

  /**
   * ID
   * @param {string[]} tmplIds - id
   * @returns {Promise}
   */
  async subscribeNews(tmplIds) {
    return new Promise((resolve, reject) => {
      wx.requestSubscribeMessage({
        tmplIds: tmplIds,
        success: (res) => {
          if (res[tmplIds[0]] === "accept") {
            this._showToast("", resolve);
          } else {
            this._showAuthorizationModal(tmplIds);
          }
        },
        fail: (err) => {
          console.error("", err);
          reject(err);
        },
      });
    });
  }

  _showToast(message, callback) {
    wx.showToast({
      title: message,
      duration: 1000,
      success() {
        console.log("");
        callback();
      },
    });
  }

  _showAuthorizationModal(tmplIds) {
    wx.showModal({
      content: "",
      confirmText: "",
      cancelText: "",
      success: (result) => {
        if (result.confirm) {
          this.subscribeNews(tmplIds);
        } else {
          console.log("");
        }
      },
    });
  }
}

module.exports = MessageSubscriber;
